.student_result_section{
    width: 100%;
    height: auto;
    background-color: #f7f7f7;
    /* background-color:#189AB4; */
    /* background-image: linear-gradient(to right bottom, #189AB4, #05445E); */
    /* color: #f7f7f7; */
    padding: 100px 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.student_card--container{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.utility_center_text{
    text-align: center;
  }
  .about_section__heading{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 120px;
  letter-spacing: 3px;
  border-bottom: 5px solid rgba(46, 49, 65,1);
  display: inline-block;
  }
.student_card{
    width: 220px;
    height: 260px;
    position: relative;
    border-radius: 6px;
    box-shadow: 0 1.5rem 4rem rgba(0,0,0,.4);
    margin: 50px;
    /* border-top: 6px solid white; */
}
.student_card--image{
    width: 100%;
    height: 100%;
    border-radius: 6px;
    object-fit: cover;
}
.student_text-container{
position: absolute;
bottom:-20px;
left:50%;
transform: translateX(-50%);
width: 80%;
background-color:#189AB4;
color:white;
border-radius: 4px;
padding: 5px;
overflow: hidden;
height: 6vh;
transition: all 0.5s ease;
display: flex;
flex-direction: column;
align-items: center;
}
.student_text{
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 1.6rem;
}
.student_name{
    transform: translateY(-100px);
    font-size: 1rem;
    text-align: center;
}
.student_card:hover .student_name{
    transform: translateY(0px);   
}
.student_card:hover .student_text-container{
    height: 12vh;
    justify-content: center;
}
.batch_title{
    font-size: 3rem;
    color:#189AB4;
    opacity: 0.8;
    text-shadow: 0px 2px 4px #000000;
    border-bottom: none;
    margin: 1.5rem 0;
}
.utility_margin{
    margin: 5rem 0;
}
.result_percentage{
    color:red;
    text-shadow: 0px 2px 4px #000000;
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
    text-transform: uppercase;
    letter-spacing: 2px;
}
.utility_margin-1{
    margin: 5rem 0 1.5rem 0;
}
@media (max-width:639px) {
    .student_name{
        transform: translateY(0px);
        font-size: 1rem;
    }
    .student_text-container{
        height: auto;
    }
    .student_text-container{
        padding: 10px 5px;
    }
}